
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'

import whitespace from '../base/whiteSpace'
import axios from 'axios'
export default {
data(){
return{
options:['',''],
type: '',
name: '',
password: '',
repeatpass: '',
tip:'',
tipshow: false,
}
},
components:{
XInput,
XButton,
whitespace,
Radio,
Group,
},
methods: {
doregister(){
var img = this.$store.state.img
var info = {
name: this.name,
password: this.password,
type: this.type,
img: img,
}
info = JSON.stringify(info)
var that = this
if(this.checkinput()){
axios({
method: 'get',
url: 'http://localhost:9093/user/register',
params:{info}
}).then(function(res){
if(res.data.code == '1'){
that.tip = ''
that.tipshow = true
}else{
var store = that.$store
store.commit('setname',that.name)
store.commit('settype',that.type)
if(res.data.type == ''){
that.$router.push('/employerinfo')
}else{
that.$router.push('/employeeinfo')
}
}
})
}else{

}
},
checkinput(){
if(this.name.length > 4 &&this.password!='' &&  this.password == this.repeatpass){
return true
}else{
if(this.name.length < 4 ){
this.tip = '5,'
this.tipshow = true
}else if(this.password == ''){
this.tip = ''
this.tipshow = true
}else if(this.password != this.repeatpass){
this.tip = ''
this.tipshow = true
}
return false
}
},
inputchange(){
this.tip = ''
this.tipshow = false
}
}
}



