
import XHeader from 'vux/src/components/x-header/index.vue'

import axios from 'axios'
export default {
name: 'news',
data(){
return {
chathistory: [{name:''}, {name: ''}],

}
},
components: {
XHeader,
},
methods: {
handleclick(item){
this.$store.commit('setchatwith',item)
this.$router.push('/newlist')
}
},
mounted(){
var name = this.$store.state.name;
var that = this
axios({
method: 'get',
url: 'http://localhost:9093/user/news',
params: {name}
}).then(function(res){
that.chathistory = res.data
that.chathistory.forEach(item=>{
if(item.with == name){
item.with = item.name
}
})
})
}
}



