
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

export default {
name: 'chooseimg',
data(){
return{
imginfo: ['beauty','boy','girl','glass', 'man','oldboy','oldlady','women'],
chooseimg:"",
tip: ''
}
},
components: {
Grid,
GridItem ,
},
methods: {
handimg(item){
return './../../../static/' + item +'.jpg'
},
handlechoose(){
if(this.chooseimg){
this.tip = ''
return './../../../static/' + this.chooseimg +'.jpg'
}
},
handleclick(item){
this.$store.commit('setimg',item)
this.chooseimg = item
},
}
}



