
import Card from 'vux/src/components/card/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import axios from 'axios'
export default {
data(){
return {
list: [],
type: '',
listheader: ''
}
},
components: {
Card,
XHeader
},
mounted(){
this.listheader = this.$store.state.type
var name = this.$store.state.name
var that = this
this.type = this.$store.state.type
axios({
method: 'get',
url: 'http://localhost:9093/user/joblist',
params: {'name':name}
}).then(function(res){
if(res.data instanceof Array){
that.list = res.data
}

})
},
methods:{
tochat(name){
this.$router.push('/newlist')
this.$store.commit('setchatwith', name)
}
}
}



