
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import chooseimg from '../base/chooseImg'
import whitespace from '../base/whiteSpace'
import axios from 'axios'
export default {
name: 'employeeinfo',
data(){
return{
job: '',
des: '',
tipshow: false,
tip: ''
}
},
components: {
XInput,
chooseimg,
XButton,
whitespace
},
methods: {
saveinfo(){
var that = this
var name = that.$store.state.name
if(this.job && this.des){
axios({
methods: 'get',
url: 'http://localhost:9093/user/eeinfo',
params:{name:name,job:that.job,des:that.des}
}).then(function(res){
if(res.data.code == '0'){
that.$router.push('/list')
}
})
}else{
this.tipshow = true
}
},
inputchange(){
this.tipshow = false
}
}
}



