import axios from 'axios'
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted(){
    axios.get('http://localhost:9093/')
      .then(res => {
        console.log(res)
      })
  }
}



