
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import whitespace from '../base/whiteSpace'
import axios from 'axios'
export default {
data(){
return {
name: '',
password: '',
alertshow: false,
alertcontent: '',
tipshow: false
}
},
components:{
XInput,
XButton,
whitespace,
Alert
},
methods:{
tologin(){
var that = this
var info = JSON.stringify({
name: this.name,
password: this.password
})
if(this.name.length>4 && this.password!=''){
axios({
method: 'get',
url: 'http://localhost:9093/user/login',
params:{info}
}).then(function(res){
if(res.data.code == '1'){
that.tipshow = true
}else{
var doc = res.data.doc
var type = doc.type
var des = doc.des
var store = that.$store
var obj = {
name: that.name,
type: type,
des: des,
}
console.log(doc)
obj = JSON.stringify(obj)
store.commit('setname',that.name)
store.commit('settype',type)
store.commit('setdes', des)
that.$router.push('/list')
window.sessionStorage.setItem('info',obj)
}
}).catch(function(err){console.log(err)})
}else{
if(this.name == '' || this.password == ''){
this.alertcontent = ''
this.alertshow = true
}else{
this.alertcontent = '5'
this.alertshow = true
}
}
}
}
}



